
import { Options, Vue } from "vue-class-component";
import { store } from "@/store";
import axios from "axios";
import { OwnedGame } from "@/models/game";
import Settings from "./components/Settings.vue";
import Rate from "./Rate.vue";
import Rec from "./Rec.vue";

class Prop {
  userId!: string;
}

@Options({
  components: {
    Settings,
    Rate,
    Rec,
  },
})
export default class Dashboard extends Vue.with(Prop) {
  private settingsDialog = false;
  private isRate = true;

  created() {
    this.getAllGamesWithRatings();
  }

  private getAllGamesWithRatings() {
    Promise.all([
      axios.get("https://steamrec.cyclic.app/api/owned-games"),
      axios.get("https://steamrec.cyclic.app/data/ratings"),
    ]).then(([gameRes, ratingRes]) => {
      const gameData = gameRes.data as [OwnedGame];
      const ratingData = ratingRes.data as [{ gameId: number; rating: number }];

      gameData.forEach((game) => {
        game.rating =
          ratingData.find((rating) => rating.gameId == game.id)?.rating ?? 0;

        store.commit("addOwnedGame", game);
      });
    });
  }

  private onClickRate() {
    this.isRate = true;
  }

  private onClickRec() {
    this.isRate = false;
  }

  private openSettingsDialog() {
    this.settingsDialog = true;
  }

  private closeSettingsDialog() {
    this.settingsDialog = false;
  }

  private logout() {
    window.location.replace("https://steamrec.cyclic.app/logout");
    store.commit("clear");
  }
}
